.cart {
  background-color: #fdf7e7;
  width: 90%;
  height: 40px;
  border-bottom: 1.5px solid rgb(94, 94, 94);
  padding: 4px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  animation: cartSwitchAnim 0.4s ease-out;
}
.cart span {
  font-weight: bold;
  margin-left: 10px;
}
.cart .cart-clickable-area:hover .cart {
  background-color: #c7bfa9;
  margin-left: 50px;
}
.cart button {
  position: relative;
  background: none;
  border: none;
  cursor: pointer;
}
.cart button .switch-icon {
  height: 20px;
  width: 20px;
  padding: 10px;
}
.cart button .chart-icon {
  height: 20px;
  width: 20px;
  padding: 10px;
}
.cart .switch-button,
.cart .chart-button {
  visibility: hidden;
}
.cart button::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.cart .switch-button:hover .switch-icon {
  color: #6e6e6e;
}
.cart .chart-button:hover .chart-icon {
  color: #6e6e6e;
}

.cart:hover .switch-button {
  visibility: visible;
}

.cart:hover .chart-button {
  visibility: visible;
}

@keyframes cartSwitchAnim {
  from {
    background-color: #76f35d;
  }
  to {
    background-color: #fdf7e7;
  }
}